import React, { Component } from 'react'
import { withFirebase } from '../Firebase/'
import { ReactComponent as Heart } from '../assets/heart-solid.svg'
import './List.css'

const TABS = [
    {
        title: 'Todo',
        db: 'todo',
        index: 0
    },
    {
        title: 'Titta',
        db: 'watch',
        index: 1
    },
    {
        title: 'Mat',
        db: 'food',
        index: 2
    }
]

class List extends Component {
    state = {
        data: {},
        selectedTab: TABS[0],
        inputValue: ''
    }

    componentDidMount() {
        this.props.firebase.doGetList(data => this.setState({ data }))
    }

    handleNewTodo = event => {
        event.preventDefault()
        const { inputValue, selectedTab } = this.state
        if (inputValue.length > 0) {
            this.setState({ inputValue: '' })
            const todo = {
                value: inputValue,
                isChecked: false
            }
            this.props.firebase.doAddNewTodo(todo, selectedTab.db)
        }
    }

    handleRemoveTodo = index => {
        const { data, selectedTab } = this.state
        this.props.firebase.doRemoveTodo(
            data[selectedTab.db][index],
            selectedTab.db
        )
    }

    handleCheckTodo = index => {
        const { data, selectedTab } = this.state

        data[selectedTab.db][index].isChecked = !data[selectedTab.db][index]
            .isChecked

        this.props.firebase.doMarkAsChecked(
            data[selectedTab.db],
            selectedTab.db
        )
    }

    handleTabChange = index => {
        this.setState({ selectedTab: TABS[index] })
    }

    render() {
        const { data, inputValue, selectedTab } = this.state
        const todos = data[selectedTab.db] ? data[selectedTab.db] : []
        todos.sort((a, b) =>
            a.isChecked === b.isChecked ? 0 : b.isChecked ? -1 : 1
        )
        return (
            <div className="list-container">
                <h1>Saker Att GÃ¶ra Medan Man Pussas</h1>
                <form onSubmit={this.handleNewTodo}>
                    <input
                        type="text"
                        value={inputValue}
                        onChange={e =>
                            this.setState({ inputValue: e.target.value })
                        }
                    />
                    <button onClick={this.handleNewTodo}>
                        <Heart />
                    </button>
                </form>
                <div className="tabs">
                    {TABS.map((tab, index) => (
                        <div
                            key={index}
                            className={
                                this.state.selectedTab.index === index
                                    ? 'tab active'
                                    : 'tab'
                            }
                        >
                            <button onClick={() => this.handleTabChange(index)}>
                                {tab.title}
                            </button>
                        </div>
                    ))}
                </div>
                <div className="list">
                    <ul>
                        {todos.map((todo, index) => (
                            <li className={todo.isChecked
                                            ? 'list-item checked'
                                            : 'list-item'} key={index}>
                                <span>{todo.value}</span>
                                <button
                                    className={
                                        todo.isChecked
                                            ? 'checkbox active'
                                            : 'checkbox'
                                    }
                                    onClick={() => this.handleCheckTodo(index)}
                                />
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        )
    }
}

export default withFirebase(List)
